export const mobile = (text = '') => {
  return {
      trigger: 'blur',
      validator(rule, value, callback) {
        let error = []
        if (!(/^1[34578]\d{9}$/.test(value))) {
          error.push(new Error(text))
        }
        callback(error);
      }
    }
}
export const empty = (text = '') => {
  return {
    trigger: 'blur',
    validator: (rule, value, callback) => {
      if (value === '') {
        callback(new Error(text))
      }
      callback()
    }
  }
}

export const textLength = (args) => {
  return {
    trigger: 'change',
    validator: (rule, value, callback) => {
      if (args.max && value.length > args.max) {
        const text = args.maxText ? args.maxText : `${args.max}`
        callback(new Error(text))
        return
      }
      if (args.min && value.length < args.min) {
        const text = args.minText ? args.minText : `${args.min}`
        callback(new Error(text))
        return
      }
      if (args.num && value.length != args.num) {
        const text = args.numText ? args.numText : `${args.num}`
        callback(new Error(text))
        return
      }
      callback()
    }
  }
}

// 
export const number = (text = '') => {
  return {
    trigger: 'blur',
    validator(rule, value, callback) {
      if (!/^[0-9]*$/.test(value)) {
        callback(new Error(text))
      }
      callback()
    }
  }
}
/* eslint-disable */
// 
export const flowNumber = (text = '') => {
  return {
    trigger: 'blur',
    validator(rule, value, callback) {
      if (!/^\d+(\.\d+)?$/.test(value)) {
        callback(new Error(text))
      }
      callback()
    }
  }
}
// 2
export const flowNumber2 = (text = '') => {
  return {
    trigger: 'blur',
    validator(rule, value, callback) {
      if (!/^\d+(\.\d{1,2})?$/.test(value)) {
        callback(new Error(text))
      }
      callback()
    }
  }
}
// 2
export const passWord = (text = '+ 6') => {
  return {
    trigger: 'change',
    validator(rule, value, callback) {
      var reg = RegExp(/[A-Za-z].*[0-9]|[0-9].*[A-Za-z]/)
      if (!reg.test(value)) {
          callback(new Error(text))
          return
      }
      if (value.length < 6) {
        callback(new Error('6'))
        return
      }
      callback()
    }
  }
}


export const abs0 = (absVal= 1.5) => {
  return {
    trigger: 'blur',
    validator(rule, value, callback) {
      if (!/^[0-9]*$/.test(Number(value) / absVal)) {
        callback(new Error(`${absVal}`))
      }
      callback()
    }
  }
}
export const get0 = (val = 1.5) => {
  return {
    trigger: 'blur',
    validator(rule, value, callback) {
      if (Number(value) < val) {
        callback(new Error(`${val}`))
      }
      callback()
    }
  }
}



